import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from './../components/HelloWorld.vue'
import Index from './../components/index.vue'

Vue.use(Router)

const routers = [
    {
        path: '/',
        component: HelloWorld
    },
    {
        path: '/index',
        component: Index
    }
]   
let router = new Router({
    // mode: 'history',
    routes: routers
})  
export default router 